
@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@1,300&display=swap');



:root{
  --first-color : hsl(353,100%, 65%);
  --title-color : hsl(244,24%,26%);
  --text-color :  hsl(244,16%,43%);
  --body-color :  hsl(258,60%,98%);
  --container-color: #fff;


  --body-font : 'Rubik', sans-serif;
  --h1-font-size: 2.25rem;
  --h2-font-size: 1.5rem;
  --h3-font-size: 1.25rem;
  --normal-font-size: 1rem;
  --small-font-size: 0.875rem;
  --smaller-font-size: 0.813rem;

  
  --font-medium: 500;
  --font-semibold: 600;
  --font-bold: 700;

  
  --shadow: 0px 5px 20px 0px rgba(69 67 96/ 10%);

 
  --border-radius:20px;

}

 @media screen  and (max-width:1024px){
  :root{
  --h1-font-size: 1.75rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1rem;
  --normal-font-size: 0.938rem;
  --small-font-size: 0.813rem;
  --smaller-font-size: 0.75rem;
  }
 }


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


html{
  scroll-behavior: smooth;
}

body,
button,
input,
textarea{
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
}

body{
  background-color: rgb(255, 224, 235);
  line-height: 1.7;
  color: var(--text-color);
}

h1,h2,h3{
  color: var(--title-color);
  font-weight: var(--font-semibold);
  line-height: 1.2;
}
li{
  list-style: none;
}

ul{
  list-style: none;
}

a{
  text-decoration: none;
}

button{
  cursor: pointer;
  border: none;
  outline: none;
}

img{
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}


.container{
  max-width: 1180px;
  padding-left: 15px;
  padding-right: 15px;
  margin: 0;
}

.grid{
  display: grid;
}

.section{
  padding-top: 7rem;
  padding-bottom: 2rem;
}

.section__title{
  font-size: var(--h1-font-size);
  margin-left: 0.875rem;
  font-weight: var(--font-bold);
  position: relative;
  margin-bottom: 3.75rem;
}

.section__title::before{
  content: '';
  background: url(./assets/dots-bg.svg);
  height: 2.25rem;
  width: 2.25rem;
  position: absolute;
  left: -0.875rem;
  top: -0.875rem;
}

.btn{
  padding: 0.75rem 2rem;
  line-height: 1;
  border-radius: 1.875rem;
  box-shadow: 0 0 10x rgb(0 0 0/0%);
  border: 1px solid transparent;
  color: #fff;
  display: inline-block;
  background-color: var(--first-color);
  font-weight: var(--font-bold);

}

@keyframes button-psh{
  50%{
    transform: scale(0.8);
  }
  100%{
    transform: scale(1);
  }
}

.btn:hover{
  background-color: hsl(43, 15%, 33%);
  animation: button-push 0.3s linear 1;
}

 
.threedtitle{
  font-size: 3rem;
  
  letter-spacing: 0.5rem;
  text-shadow: 4px 4px rgb(216, 179, 192),
              6px 6px rgb(78, 4, 30);
  -webkit-text-fill-color: transparent;
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: rgb(219, 73, 124);
  

}

@media screen and (max-width:575px){
  .threedtitle{
      font-size: 2rem;
      letter-spacing: 0.3rem;
      text-shadow: 3px 3px rgb(207, 209, 183),
              4px 4px black;
  }
}


@media screen and (max-width:410px){
  .threedtitle{
      font-size: 1.4rem;
      letter-spacing: 0.1rem;
      text-shadow: 3px 3px rgb(207, 209, 183),
              4px 3px black;
  }
}



  

