.aside{
    position: fixed;
    left: 0;
    top: 0;
    /* background-color: rgb(255, 224, 235); */
    background-color: transparent;
    padding: 2.5rem;
    width: 110px;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    z-index: 10;
    transition: .3s ;
}

.nav__list{
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
}

.nav__link{
    font-size: 1.5rem;
    color: var(--title-color);
    font-weight: var(--font-bold);
}

.nav__link:hover{
    color: hsl(313, 75%, 70%);
}

.nav__toggle{
    position: fixed;
    top: 1.25rem;
    left: 1.875rem;
    cursor: pointer;
    height: 40px;
    width: 45px;
    background-color: var(--body-color);
    border: 1px solid #e8dfec;
    display: none;
    justify-content: center;
    align-items: center;
    z-index: 1;
}

@media  screen and (max-width: 1024px) {
    .aside{
        left: -110px;
    }


    .nav__toggle{
        display: flex;
    }
    
    .nav__toggle-open{
        left: 140px;

    }

    .show-menu{
        left: 0;
    }
}